/**
 * leeappparent\web\src\views\order\order_content\order_content.vue
 */
const MIXINS_FORMRULE = {
  methods: {
    ruleForm(formName) {
      let data = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          data = this[formName]
        } else {
          data = false
        }
      })
      return data
    },
  }
}

/**
 * leeappparent\web\src\views\marketing\mark\bargains\add_bargains\add_bargains.vue
 * leeappparent\web\src\views\marketing\mark\team_buy\add_team_buy\add_team_buy.vue
 */
const MIXINS_BARGAINS_AND_TEAM_BUY_FORMRULES = {
  methods: {
    // 表单验证
    _formRules () {
      if (this.goodsAndActivity) {
        for (let item of this.goodsAndActivity) {
          if (Number(item.discountRate) < 0 && Number(item.restrictionQuantity) < 0 && Number(item.postFee) < 0) {
            this.$message.error('请把商品信息填写完整')
            return
          }
          if (item.discountRate > 1) {
            this.$message.error('折扣率不能大于1')
            return
          }
        }
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          return true
        } else {
          console.log('error submit!!')
          return
        }
      })
      return true
    },
  }
}

/**
 * 通用的表单验证
 * wohoWeb\src\views\shop\shop_industry\list.vue
 * wohoWeb\src\views\post_fee\supermarket\add_postfee_cmp\index.vue
 * wohoWeb\src\views\post_fee\supermarket\edit_postfee_cmp\index.vue
 */
const MIXINS_BASIC_FORMRULES = {
  methods: {
    _ruleForm (formName) {
      let data = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          data = true
        } else {
          data = false
        }
      })
      return data
    },
  }
}

/**
 * src\views\super_market\mark\bargains\add_bargains\add_bargains.vue
 */
const MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_FORMRULES = {
  methods: {
    // 表单验证
    _formRules () {
      if (this.goodsAndActivity) {
        for (let item of this.goodsAndActivity) {
          // eslint-disable-next-line
          if (typeof item.limited != 'number') {
            this.$message.error('请把商品信息填写完整')
            throw SyntaxError('请把商品信息填写完整')
          }
        }
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          return true
        } else {
          throw SyntaxError('活动详情填写完整')
        }
      })
      return true
    },
  }
}

export {
  MIXINS_FORMRULE,
  MIXINS_BARGAINS_AND_TEAM_BUY_FORMRULES,
  MIXINS_BASIC_FORMRULES,
  MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_FORMRULES
}
