const MIXINS_BARGAINS_AND_TEAM_BUY_DATA = {
  data() {
    return {
      form: {
        startTime: '', // 活动开始时间
        endTime: '', // 活动结束时间
        activeDescribe: '', // 活动描述
        activeName: '' // 拼团/秒杀标题
      },
      rules: {
        activeName: [
          { required: true, message: '请输入标题', trigger: 'blur' }
        ],
        startTime: [
          { required: true, message: '请选择活开始动时间', trigger: 'change' }
        ],
        endTime: [
          { required: true, message: '请选择活动结束时间', trigger: 'change' }
        ],
        activeDescribe: [
          { required: true, message: '请输入活动描述', trigger: 'blur' }
        ]
      },
      goodsTotle: 0, // 商品分页总数
      oldUpdateIndex: '', // 已关联商品的编辑哪一行
      updateIndex: '', // 新增关联商品的编辑哪一行
      dialogVisible: false, // 是否打开添加商品的窗口
      goodsAndActivity: [], // 新增关联的商品列表
      blUpdata: false, // 判断当前页面是否是编辑页面
      oldGoodsAndActivity: [] // 已关联的商品列表
    }
  },
}

const MIXINS_BTNPERMISSIONS_DATA = {
  data() {
    return {
      btnPermissions: localStorage.getItem('btnPermission').split(','), // 按钮权限
    }
  },
}

/**
 * src\views\super_market\mark\bargains\add_bargains\add_bargains.vue
 */
const MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_DATA = {
  data() {
    const checkDiscount = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('折扣率不能为空'))
      }
      if (!Number.isInteger(value)) {
        return callback(new Error('请输入数字值'))
      } else if (value > 100 || value < 1) {
        return callback(new Error('不能大于100或者小于1'))
      }
    }
    return {
      activeType: 1, // 活动类型 0-无活动 1-秒杀 2-团购
      timeDate: '', // 活动开始时间日期
      time: '', // 活动开始时间时分
      form: {
        secondsStartTime: '', // 活动开始时间
        secondsStopTime: '', // 活动结束时间
        whenLength: 4, // 活动持续时长 默认4小时
        activeDescribes: '', // 活动描述
        secondsKillName: '', // 拼团/秒杀标题
        discount: '', // 优惠率 百分比
      },
      rules: {
        secondsKillName: [
          { required: true, message: '请输入标题', trigger: 'blur' }
        ],
        secondsStartTime: [
          { required: true, message: '请选择活动时间', trigger: 'change' }
        ],
        whenLength: [
          { required: true, message: '请输入活动持续时长', trigger: 'blur' }
        ],
        activeDescribes: [
          { required: true, message: '请输入活动描述', trigger: 'blur' }
        ],
        discount: [
          { validator: checkDiscount, trigger: 'blur' }
        ]
      },
      goodsTotle: 0, // 商品分页总数
      oldUpdateIndex: '', // 已关联商品的编辑哪一行
      updateIndex: '', // 新增关联商品的编辑哪一行
      dialogVisible: false, // 是否打开添加商品的窗口
      goodsAndActivity: [], // 新增关联的商品列表
      blUpdata: false, // 判断当前页面是否是编辑页面
      oldGoodsAndActivity: [] // 已关联的商品列表
    }
  },
}

export {
  MIXINS_BARGAINS_AND_TEAM_BUY_DATA,
  MIXINS_BTNPERMISSIONS_DATA,
  MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_DATA,
}
