const MIXINS_BARGAINS_AND_TEAM_BUY_ADDALL = {
  /**
   * leeappparent\web\src\views\marketing\mark\bargains\add_bargains\add_bargains.vue
   * leeappparent\web\src\views\marketing\mark\team_buy\add_team_buy\add_team_buy.vue
   */
  methods: {
    // 点击添加（批量）
    addAll () {
      let successCut = 0 // 成功的数量
      let errCut = 0 // 失败的数量
      for (let i = 0; i < this.$refs.AddGoodsCmp.$data.multipleSelection.length; i++) {
        if (this.goodsAndActivity.includes(this.$refs.AddGoodsCmp.$data.multipleSelection[i])) {
          errCut++
        } else {
          successCut++
          this.$refs.AddGoodsCmp.$data.multipleSelection[i].prodId = this.$refs.AddGoodsCmp.$data.multipleSelection[i].id
          this.$refs.AddGoodsCmp.$data.multipleSelection[i].id = ''
          this.goodsAndActivity.push(this.$refs.AddGoodsCmp.$data.multipleSelection[i])
        }
      }
      if (errCut !== 0) {
        this.$notify.error({
          title: '错误',
          message: `有${errCut}个重复了`
        })
      }
      if (successCut !== 0) {
        this.$message.success(`有${successCut}个添加成功`)
        this.dialogVisible = false
      }
    },
    // 分页
    handleCurrentChange () {

    },
    // 已关联商品的编辑
    onOldUpdate (scope) {
      this.oldUpdateIndex = scope.$index
    },
    // 编辑
    onUpdate (scope) {
      this.updateIndex = scope.$index
    },
    // 已关联商品的完成
    onOldSave() {
      this.oldUpdateIndex = ''
    },
    // 完成
    onSave(scope) {
      this.updateIndex = ''
    },
    // 删除
    onDelete(scope) {
      this.goodsAndActivity.splice(scope.$index, 1)
    },
    // 添加商品
    onAddGoods() {
      this.dialogVisible = true
    },
  }
}

/**
 * 自定义组件地区选择的公共方法
 * wohoWeb\src\views\ad\ad_add\ad_add.vue
 */
const MIXINS_AREASELECT_EVNET = {
  methods: {
    // 子组件传给父组件的发货区域
    _areaSelect (e) {
      let area = e[0] + ',' + e[1]
      if (e.length === 3) {
        return area + ',' + e[2]
      }
    },
  }
}

/**
 * 单图片上传的公共方法
 * wohoWeb\src\views\commodity\category\Category.vue
 * wohoWeb\src\components\chat_window_cmp\index.vue
 */
const MIXINS_UPLOADFILE_EVNET = {
  methods: {
    // 自定义覆盖主图图片上传方法
    uploadFile (params) {
      let formData = this._uploadFile(params)
      if (formData) {
        return this.$http.post('/common/oss/uploadFile', formData).then(res => res.data.data.webUrl)
      }
    },
    // 上传图片前的处理
    _uploadFile (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG/PNG 格式!')
        return false
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10MB!')
        return false
      }
      const formData = new FormData()
      formData.append('file', params.file)
      return formData
    },
  }
}

/**
 * 公共的方法验证是否是数字
 * wohoWeb\src\views\commodity\CommitAdd.vue
 */
const MIXINS_VALIDATION_NUMBER = {
  methods: {
    // 验证是数字加小数点
    _validationNumber (obj) { // 值允许输入一个小数点和数字
      obj = obj.replace(/[^\d.]/g, '') // 先把非数字的都替换掉，除了数字和.
      obj = obj.replace(/^\./g, '') // 必须保证第一个为数字而不是.
      obj = obj.replace(/\.{2,}/g, '.') // 保证只有出现一个.而没有多个.
      obj = obj.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.') // 保证.只出现一次，而不能出现两次以上
      return obj
    },
    // 验证是数字
    _verifyNumber (obj) { // 值允许输入一个小数点和数字
      obj = obj.replace(/[^\d]/g, '') // 先把非数字的都替换掉，除了数字和.
      return obj
    },
    // 验证是金额 后面两位小数点
    _validationMoney (obj) { // 值允许输入一个小数点和数字
      obj = obj.replace(/[^\d.]/g, '')
      obj = obj.replace(/^0\d+|^\./g, '')
      obj = obj.replace(/\.{2,}/g, '.')
      obj = obj.replace('.', '$#$')
      obj = obj.replace(/\./g, '')
      obj = obj.replace('$#$', '.')
      obj = obj.replace(/^(\d+)\.(\d\d).*$/, '$1.$2')
      return obj
    },
  }
}

