<template>
  <div class="box">
    <LayoutBox one="新增活动">
      <h4>商品介绍</h4>
      <el-form ref="form" label-width="100px" :model="form" :rules="rules" size="small">
        <el-form-item label="拼团标题" prop="activeName">
          <el-input v-model="form.activeName" style="width: 454px" maxlength="30" show-word-limit />
        </el-form-item>
        <el-form-item label="活动时间" required>
          <div class="time_group">
            <el-form-item prop="startTime">
              <el-date-picker v-model="form.startTime" type="datetime" placeholder="选择活动开始时间" />
            </el-form-item>
            <span>至</span>
            <el-form-item prop="endTime">
              <el-date-picker v-model="form.endTime" placeholder="选择活动结束时间" type="datetime" />
            </el-form-item>
          </div>
        </el-form-item>
        <el-form-item label="活动描述" prop="activeDescribe">
          <el-input v-model="form.activeDescribe" style="width: 454px" />
        </el-form-item>
      </el-form>
    </LayoutBox>
    <el-card v-if="blUpdata" class="box-card">
      <h4>已关联商品</h4>
      <el-table :data="oldGoodsAndActivity" style="width: 100%">
        <!-- <el-table-column prop="date" label="商品编号" /> -->
        <el-table-column prop="name" label="商品名称" />
        <el-table-column prop="shopId" label="供应商" />
        <el-table-column prop="originalPrice" label="销售价（元）" />
        <el-table-column label="拼团折扣率（元）">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" v-model="scope.row.discountRate" />
            <p v-else>{{ scope.row.discountRate }}</p>
          </template>
        </el-table-column>
        <!-- <el-table-column label="拼团价（元）">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" />
            <p v-else>{{ scope.row.date }}</p>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="成团人数">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" />
            <p v-else>{{ scope.row.amount }}</p>
          </template>
        </el-table-column> -->
        <el-table-column label="限购量">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" v-model="scope.row.restrictionQuantity" />
            <p v-else>{{ scope.row.restrictionQuantity }}</p>
          </template>
        </el-table-column>
        <el-table-column label="运费">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" v-model="scope.row.postFee" />
            <p v-else>{{ scope.row.postFee }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="操作" width="150">
          <template slot-scope="scope">
            <el-button v-if="oldUpdateIndex === scope.$index" type="text" @click="onOldSave">完成</el-button>
            <el-button v-else type="text" @click.native.prevent="onOldUpdate(scope)">编辑</el-button>
            <el-button type="text">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="goodsTotle" @current-change="handleCurrentChange" />
    </el-card>
    <el-card class="box-card">
      <h4>新增关联商品</h4>
      <el-table :data="goodsAndActivity" style="width: 100%">
        <!-- <el-table-column prop="code" label="商品编号" /> -->
        <el-table-column prop="name" label="商品名称" />
        <el-table-column prop="shopId" label="供应商" />
        <el-table-column prop="originalPrice" label="销售价（元）" />
        <el-table-column label="拼团折扣率">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model="scope.row.discountRate" type="number" />
            <p v-else>{{ scope.row.discountRate }}</p>
          </template>
        </el-table-column>
        <!-- <el-table-column label="拼团价">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model="scope.row.price" type="number" />
            <p v-else>{{ scope.row.price }}</p>
          </template>
        </el-table-column> -->
        <el-table-column label="限购量">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model.number="scope.row.restrictionQuantity" type="number" />
            <p v-else>{{ scope.row.restrictionQuantity }}</p>
          </template>
        </el-table-column>
        <el-table-column label="运费">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model="scope.row.postFee" type="number" />
            <p v-else>{{ scope.row.postFee }}</p>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button v-if="updateIndex === scope.$index" type="text" @click="onSave(scope)">完成</el-button>
            <el-button v-else type="text" @click.native.prevent="onUpdate(scope)">编辑</el-button>
            <el-button type="text" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="add_goods_btn">
        <el-button class="add_goods" type="text" icon="el-icon-plus" @click.native.prevent="onAddGoods">添加商品</el-button>
      </div>
      <el-divider />
      <div class="btn_group">
        <el-button v-if="blUpdata" class="inquire" type="success" size="medium" @click="save_btn">保存</el-button>
        <el-button v-else class="inquire" type="success" size="medium" @click="submit_btn">提交</el-button>
        <router-link to="/mark/list/teamBuyList"><el-button size="medium" style="width: 112px">取消</el-button></router-link>
      </div>
    </el-card>
    <el-dialog title="提示" :visible.sync="dialogVisible" fullscreen>
      <AddGoodsCmp ref="AddGoodsCmp" />
      <span slot="footer" class="dialog-footer">
        <el-button round @click="dialogVisible = false">取 消</el-button>
        <el-button type="danger" round @click="addAll()">添 加</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import AddGoodsCmp from '@/views/marketing/mark/add_goods_cmp/index'
import { MIXINS_BARGAINS_AND_TEAM_BUY_FORMRULES } from '@/mixins/form.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_ADDALL } from '@/mixins/event.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_APIINSERTMERACTIVITY } from '@/mixins/api.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_DATA } from '@/mixins/data.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_CREATED } from '@/mixins/created.js'

export default {
  name: 'AddTeamBuy',

  components: {
    LayoutBox, AddGoodsCmp
  },

  data() {
    return {
      activeType: 0, // 活动类型 0-团购 1-秒杀
    }
  },

  mixins: [
    MIXINS_BARGAINS_AND_TEAM_BUY_FORMRULES,
    MIXINS_BARGAINS_AND_TEAM_BUY_ADDALL,
    MIXINS_BARGAINS_AND_TEAM_BUY_APIINSERTMERACTIVITY,
    MIXINS_BARGAINS_AND_TEAM_BUY_DATA,
    MIXINS_BARGAINS_AND_TEAM_BUY_CREATED,
  ],

  methods: {
    // 点击提交
    submit_btn () {
      if (this.goodsAndActivity.length < 1) {
        this.$message.error('请先选择添加商品')
        return
      }
      if (this._formRules()) {
        this.apiInsertMerActivity().then((res) => {
          if (res.data.success) {
            this.$message.success('成功发布活动')
            this.$router.push('/mark/list/teamBuyList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    },
    // 点击保存
    save_btn () {
      if (this._formRules()) {
        this.apiUpdateMerActivity().then((res) => {
          if (res.data.success) {
            this.$message.success('更新活动成功')
            this.$router.push('/mark/list/teamBuyList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box{
    min-width: 1100px;
    .time_group{
      display: flex;
    }
    .box-card{
      width: 100%;
      margin-top: 15px;
      .add_goods_btn{
        width: 100%;
        text-align: center;
        .add_goods{
          color: $themColor;
        }
      }
      .btn_group{
        width: 100%;
        text-align: center;
      }
    }
  }
</style>
